.toggle_radio {
    position: relative;
    background-color: #f7f7f7;
    padding: 0 !important;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
    position: relative;
    height: 40px;
    width: 390px;
    margin: auto;
}

.toggle_radio_renewal {
    width: 260px;
}

.toggle_radio input[type=radio] {
    display: none;
}

.toggle_radio label {
    font-size: 1rem;
    color: #65656a;
    z-index: 1001;
    display: block;
    width: 130px;
    margin: 8px 2px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
    cursor: pointer;
    z-index: 1;
    text-align: center;
}

.toggle_option_slider {
    width: 130px;
    height: 40px;
    position: absolute;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    border-radius: 50px;
    -webkit-transition: all .4s ease;
    -moz-transition: all .4s ease;
    -o-transition: all .4s ease;
    -ms-transition: all .4s ease;
    transition: all .4s ease;
}

#first_toggle:checked~.toggle_option_slider,
#firstrenewal_toggle:checked~.toggle_option_slider {
    background-image: linear-gradient(#3cb9f1, #2791e7);
    left: 0;
    -webkit-box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
    -moz-box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
    box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
}

#second_toggle:checked~.toggle_option_slider,
#secondrenewal_toggle:checked~.toggle_option_slider {
    background-image: linear-gradient(#3cb9f1, #2791e7);
    left: 129px;
    -webkit-box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
    -moz-box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
    box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
}

#third_toggle:checked~.toggle_option_slider {
    background-image: linear-gradient(#3cb9f1, #2791e7);
    left: 259px;
    -webkit-box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
    -moz-box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
    box-shadow: 0 2px 5px 0 rgba(39, 39, 39, .5);
}

#first_toggle_label,
#firstrenewal_toggle_label {
    position: absolute;
    left: 0;
}

#second_toggle_label,
#secondrenewal_toggle_label {
    position: absolute;
    left: 129px;
}

#third_toggle_label {
    position: absolute;
    left: 259px;
}

#first_toggle:checked~#first_toggle_label,
#second_toggle:checked~#second_toggle_label,
#third_toggle:checked~#third_toggle_label {
    color: #fff;
}

#firstrenewal_toggle:checked~#firstrenewal_toggle_label,
#secondrenewal_toggle:checked~#secondrenewal_toggle_label {
    color: #fff;
}

